.header {
    height: 50px;
    display: flex;
    align-items: right;
    background-color: black;
    position: sticky;
    top: 0;
    z-index: 100;

}

.loginOption {
    font-size: large;
}

.rightSide{
    display: flex;
    flex-direction: row;
    position: absolute;
    right: 0;   
}

.leftSide > * {
    margin-right: 25px;
}

.links {
    text-decoration: none;
    color: greenyellow;
}


.linkAbout{
    margin-right: 25px;
}