@font-face {
    font-family: 'Main Font'; 
    src: url("../fonts/Poppins-Black.ttf")
}


.home {
    background-color: white;
    font-family:"Main Font";
    left: 1%;
}


.zimboland{
    text-shadow: 0px 0px 0.3em greenyellow, 0px 0px 0.8em greenyellow;
    text-decoration: underline;
}

.home >*> h2 {
    position: relative;
    background: linear-gradient(90deg, white,5%, black, 95%, white );
    background: black;
    border-radius: 10px;
    width: 12em;
    left:10%;
    text-align: center;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.76), 0 6px 20px 0 rgba(59, 59, 59, 0.19);
}

.home > div {
    position: relative;
    left:5%;
}

.signin {
    background-color: black;
    padding-left: 1%;
    padding-right: 1%;
    border-radius: 500px;
    margin-left: 0.5em;
    margin-right: 0.5em;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.76), 0 6px 20px 0 rgba(59, 59, 59, 0.19);
}
