.itemImage{
    width: fit-content;
    border: 2px solid black;
    border-radius: 12.5px;
    display: flex;
    width: 70%;
    
    position: relative;
    align-items: center;
    background-color:rgb(255, 255, 255);
    overflow: hidden;
    margin-left: auto;
    margin-right: auto

    
    
}

.itemImage_image{
    height: 100%;
    margin-left: auto;
    margin-right: auto;
    z-index: 1;
    max-width:100%;
    max-height:100%;
}

.previousImage{
    display: block;
    position:absolute;
    left: 5px;
    font-size: 30px;
    border: 2px black solid;
    border-radius: 12.5px;
    background-color: rgba(255, 255, 255, 0.6);
    z-index: 10;
}

.nextImage{
    position:absolute;
    right: 5px;
    font-size: 30px;
    border: 2px black solid;
    border-radius: 12.5px;
    background-color: rgba(255, 255, 255, 0.6);
    z-index: 10;
}