.container {
    display: flex;
    flex-direction: row;
    justify-content: space-between
}

.container > div {
    border: black 3px solid;
    border-radius: 25px;
    margin: 10px;
    padding: 10px;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.76), 0 6px 20px 0 rgba(59, 59, 59, 0.19);
    
}

.signUp {
    display: flex;
    flex-direction: column;
    width: 50%;
}

.signIn{
    display: flex;
    flex-direction: column;
    width: 50%;
}

.image{
    object-fit: contain;
    min-width: 64px;
    min-height: 64px;

    max-width: 64px;
    max-height: 64px;
}

h1 {
    position: relative;
    background: linear-gradient(90deg, white,5%, black, 95%, white );
    background: black;
    border-radius: 10px;
    width: 30vw;
    left: 5vw;
    text-align: center;
    color: greenyellow;
    padding-bottom: 0.25em;
    
}