

.auctionScreen {
    border: 1px solid black;
    display: flex;
    overflow: hidden;
    background-color: rgb(220,225,230)
}


.leftPanel {
    padding: 10px;
    border-radius: 20px;
    border: 3px solid black;
    margin: 0px 10px 25px 10px;
    box-shadow: 0 0 10px 0px black;
    margin-top: 10px;
    width: 25%;
    height: calc(90vh - 52px);    
    position: -webkit-sticky;
    position: sticky;
    top: 1vh;

    overflow: scroll;
    overflow-x: hidden;
    
}

.leftPanel::-webkit-scrollbar {
    width: 10px;
  }

.rightPanel{
    padding: 10px;
    border-radius: 20px;
    border: 3px solid black;
    margin: 0px 10px 25px 10px;
    box-shadow: 0 0 10px 0px black;
    margin-top: 10px;
    width: 25%;
    height: calc(90vh - 52px);

    position: -webkit-sticky;
    position: sticky;
    top: 0;

    overflow: scroll;
    overflow-x: hidden;
}

.rightPanel::-webkit-scrollbar {
    width: 10px;
  }



.auctionItemHolder {
    display: flex;
    flex-direction: column;
    width: 40%;
    height: calc(100vh - 52px);
    margin-right: auto;
    margin-left: auto;
    overflow: scroll;
    overflow-x: hidden;
}
/* scroll bars */
/* width */
.auctionItemHolder::-webkit-scrollbar {
    width: 10px;
  }
  
/* Track */
.auctionItemHolder::-webkit-scrollbar-track {
    background: transparent;
  }
  
  /* Handle */
  .auctionItemHolder::-webkit-scrollbar-thumb {
    background: rgb(175, 175, 175);
    border-radius: 40px;
    height: 10px;
  }
  
  /* Handle on hover */
  .auctionItemHolder::-webkit-scrollbar-thumb:hover {
    background: #888;
  }

.auctionHeaders {
    padding: 10px;
    border-radius: 20px;
    border: 3px solid black;
    margin: 0px 10px 25px 10px;
    box-shadow: 0 0 10px 0px black;
    margin-top: 10px;     
    top:0;
    position: sticky;
    background-color: rgba(235, 235, 235, 1);
    top: 1vh;
    z-index: 1000;
}




@media (max-width:1020px) { 
    .leftPanel {
        display: none;
    }
    .auctionItemHolder {
        width: 60%
    }
}


@media (max-width:720px) { 
    .rightPanel {
        padding: 0;
        margin: 0;
        border: 0;
        min-width: 0;
        max-width: 0;
    }
    .leftPanel {

        max-width: 0;
    }
    .auctionItemHolder {
        width: 85%
    }
}